		width: calc( ( 100% / 4 ) - 16px);
	}
	.wp-block-latest-posts.columns-5 li {
		width: calc( ( 100% / 5 ) - 16px);
	}
	.wp-block-latest-posts.columns-6 li {
		width: calc( ( 100% / 6 ) - 16px);
	}
}

.wp-block-latest-posts__post-date {
	display: block;
}

.wp-block-pullquote {
	padding: 3em 0;
	text-align: center;
}

.wp-block-pullquote.alignleft, .wp-block-pullquote.alignright {
	max-width: 375px;
}

.wp-block-pullquote cite,
.wp-block-pullquote footer {
	position: relative;
}

.wp-block-subhead {
	font-size: 1.125em;
	font-style: italic;
	opacity: 0.75;
}

.wp-block-table tr:first-of-type, .wp-block-table.is-style-stripes tr:nth-child(odd) {
	background-color: var(--colors--theme--meta-light, #f4f4f4);
}

.wp-block-table tr:first-of-type {
	font-weight: bold;
}

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	word-wrap: normal;
	border: none;
}

.screen-reader-text:active, .screen-reader-text:focus {
	clip: auto;
	clip-path: none;
	display: block;
	height: auto;
	width: auto;
	z-index: 100000;
	text-decoration: none;
}

@media (min-width: 480px) {
	.alignleft {
		float: left;
		margin: 0 1.5rem 1.5rem 0;
		max-width: 50%;
	}
}

@media (min-width: 480px) {
	.alignright {
		float: right;
		margin: 0 0 1.5rem 1.5rem;
		max-width: 50%;
	}
}

.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
	clear: both;
}

.alignfull,
.alignwide {
	width: auto;
	max-width: 100vw;
	margin-top: 2em;
	margin-bottom: 2em;
}

.alignfull img,
.alignwide img {
	width: 100%;
}

@media (max-width: 1024px) {
	.alignfull {
		margin-left: calc( 50% - 50vw);
		margin-right: calc( 50% - 50vw);
	}
}

@media (min-width: 1025px) {
	.app-main--full .alignfull {
		margin-left: calc( 50% - 50vw);
		margin-right: calc( 50% - 50vw);
	}
}

@media (min-width: 768px) and (max-width: 1024px) {
	.alignwide {
		margin-left: calc( 25% - 25vw);
		margin-right: calc( 25% - 25vw);
	}
}

@media (min-width: 1025px) {
	.app-main--full .alignwide {
		margin-left: calc( 25% - 25vw);
		margin-right: calc( 25% - 25vw);
	}
}

