////
/// Nav menu component.
///
/// @group  Components
/// @author Sky Shabatura <theme@sky.camp>
/// @link   https://taproot-theme.com
////


// mixin to generate mobile nav styles
@mixin menu-header-mobile-styles( $screen ) {

    // global mobile nav styles
    &.mobile-at-#{$screen} {

        .menu__items > .menu__item:not(:first-of-type), .menu__sub-menu .menu__item {
            border-top-width: 1px;
            border-top-style: solid;
        }

        .menu__sub-menu a:before {
            content: '';
            display: inline-block;
            width: 1.25em;
        }

        .menu__sub-menu .menu__sub-menu a:before {
            width: 2.5em;
        }

        .menu__items .menu__item.has-children > a {
            padding-right: 4em;
        }

        .menu__sub-menu {
            display: none;
        }

        .dropdown-target {
            position: absolute;
            right: 0;
            top: 0;
            height: 100%;
            width: 4em;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &.mobile-type-slide .menu__items {
            right: 0;
        }
    
        // hide header nav toggle when mobile if set in customizer
        &.hidden-when-mobile .menu--toggle {
            display: none;
        }

        // if no logo or titles, fill the space and add padding. 
        &:first-child {
            flex: 100%;
            justify-content: flex-end;
            padding: 1em 0;
        }  
                
        &.mobile-type-dropdown-slide, &.mobile-type-dropdown-fade {
            position: absolute;
            justify-content: flex-end;
            height: 100%;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;   
            padding: inherit; 
    
            .menu__items {
                left: 50%;
                transform: translateX(-50%);
                width: 90%;
            }
        }        
    }
}


// mixin to generate desktop nav styles
@mixin menu-header-desktop-styles( $screen ) {

    &.mobile-at-#{$screen} {

        flex: 1 1 100%;

        .menu__items > .menu__item > .menu__sub-menu {
            transform: translateX(-50%);
            left: 50%;
            width: 10em;
            min-width: 100%;
            padding: 0.5em 1em;

            &:before {
                content: " ";
                position: absolute;
                width: 0;
                height: 0;
                z-index: 1000;
                left: 50%;
                bottom: 100%;
                margin-left: -7px;
                border: 7px solid transparent;
                border-bottom-color: inherit;
            }

            .menu--header__link {
                line-height: 1.2em;
                padding: 0.5em 0;
                font-size: 0.9em
            }
        }
    }
}


.menu--header {
    
    &__link {    
        font-size: var( --nav--header--font-size );
        line-height: var( --nav--header--line-height );
        
        &:link, &:visited {
            color: inherit;
        }
    }

    .menu--toggle {
        margin-left: 0.6em;
        order: 2;
    }
    
    // Mobile Styles

    // always mobile
    @include menu-header-mobile-styles( 'always' );

    // mobile only styles 
    @include screen('mobile') {
        @include menu-header-mobile-styles( 'mobile' );       
    };

    // tablet and under styles 
    @include screen('tablet-and-under') {
        @include menu-header-mobile-styles( 'tablet-and-under' );
    };    


    // Desktop Styles

    // never mobile styles
    @include menu-header-desktop-styles( 'never' );

    // mobile only styles 
    @include screen('tablet-and-up') {
        @include menu-header-desktop-styles( 'mobile' );
    };    

    // tablet and under styles 
    @include screen('desktop') {
        @include menu-header-desktop-styles( 'tablet-and-under' );
    };

}



