
.breadcrumbs {

    word-wrap: break-word;
	word-break: normal;
    padding: 0.4em 0;
    margin-bottom: 2rem;
    border-top: 1px solid var( --colors--theme--meta-medium );
    border-bottom: 1px solid var( --colors--theme--meta-medium );
    color: var( --colors--theme--meta-dark );

	a:link, a:visited {
        color: inherit;
    }

    a:hover {
        color: var( --colors--theme--accent );
    }

    // in mobile view, don't bother with the single post title display
    @include screen('mobile') {
		&__crumb--post{ display: none; }
    }


	// The breadcrumb title
	&__title {
		display: none;// fix this to be good with screen reader text
	}

	// The breadcrumb list
	&__trail {
        display: flex;
		list-style : none;
		margin: 0;
        padding: 0;
	}

	// Individual item
	&__crumb {
		margin-left :  0.75em;

		&:first-of-type { margin-left  : 0; }
		&:last-of-type  { margin-right : 0; }

		&::before {
			content      : "\00bb";
			margin-right : 0.75em;
		}

		&:first-of-type::before { display: none; }
	}

	// Home item
	&__crumb--home {
        
        display: flex;

        a {
            display: flex;
            align-items: center;
        }

        .taproot-icon {
            margin-top: -0.125em;
        }
	}
}


